.hero {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    padding-top: 294px;
    padding-bottom: 328px;
    position: relative;

    @include max-break('1470px') {
        padding-bottom: 290px;
        padding-top: 230px;
    }

    @include max-break('1280px') {
        padding-bottom: 230px;
    }

    @include max-break('1030px') {
        padding-top: 180px;
    }

    @include max-break('768px') {
        padding-bottom: 393px;
    }

    @include max-break('570px') {
        background-position: 83%;
    }

    &:before {
        content: '';
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(23,25,27,1) 10%, rgba(23,25,27,0.2862394957983193) 100%);

        @include max-break('768px') {
            display: block;
        }
    }

    &:after {
        content: '';
        display: none;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 404px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        background-image: url(data:image/svg+xml;base64,);

        @include max-break('470px') {
            display: block;
        }
    }

    &__container {
        position: relative;
        z-index: 1;
        box-sizing: border-box;

        &:after {
            position: absolute;
            content: '';
            display: block;
            left: 32px;
            bottom: -169px;
            width: 16px;
            height: 53px;
            z-index: 1;
            background: url(data:image/svg+xml;base64,)
                center center no-repeat;
            background-size: contain;

            @include max-break('1030px') {
                bottom: -140px;
            }

            @include max-break('768px') {
                bottom: -120px;
            }

            @include max-break('768px') {
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    &__image {
        @include max-break('1470px') {
            max-width: 860px;
            height: auto;
        }

        @include max-break('1370px') {
            max-width: 800px;
        }

        @include max-break('1280px') {
            max-width: 650px;
        }

        @include max-break('1030px') {
            max-width: 580px;
        }

        @include max-break('850px') {
            max-width: 500px;
        }

        @include max-break('768px') {
            max-width: 460px;
        }

        @include max-break('670px') {
            max-width: 420px;
        }

        @include max-break('480px') {
            max-width: 370px;
        }

        @include max-break('415px') {
            max-width: 100%;
        }
    }
}
